import React, { Component } from 'react'
import styles from './singlePost.module.css'
import {getSinglePost, addComment, getUserById, addLike,removeLike ,addFollower, removeFollower, getUser} from '../utils/authentication'
import {updateUser} from '../actions/updateUser'
import {connect} from 'react-redux'
import {updateClickedUser} from '../actions/updateClickedUSer'
import {addPosts} from '../actions/addPost'
import FavoriteIcon from '@material-ui/icons/Favorite';
import IconButton from '@material-ui/core/IconButton';
import cn from 'classnames'

export class SinglePost extends Component {
    state = {
        post:null,
        author: '',
        inputComment: '',
        commentors:[],
        commentorsComplete: false
    }

    componentDidMount(){
            getSinglePost(this.props.match.params.post_id)
            .then(res=>{
                this.setState({
                    ...this.state,
                    post: res.data.post,
                    author: res.data.author
                })
            })
    }
    componentDidUpdate=()=>{
        console.log(this.props);
        if(this.state.post !== null && this.state.commentorsComplete === false){
            this.state.post.comments.map(async(comment)=>{
             await getUserById(comment.commentor_id)
             .then(res=>{
                this.setState({
                    ...this.state,
                    commentors: [...this.state.commentors, res.data]
                })
             })
            })
            this.setState({
                ...this.state,
                commentorsComplete: true
            })
            
        }
    }

    handleInputCommentChange = (e) =>{
        this.setState({
            ...this.state,
            inputComment: e.target.value
        })
    }

    handleSubmit = (e)=>{
        const comment = {
            commentor_id: this.props.user._id,
            body: this.state.inputComment
        }
        if(this.state.inputComment){
        addComment(this.state.post,comment)
        .then(res=>{
            getSinglePost(this.props.match.params.post_id)
            .then(res=>{
            
            this.setState({
                ...this.state,
                post: res.data.post,
                commentorsComplete: false,
                commentors: '',
                inputComment: ''
            })
        })
        })
    }
       
    }
    // handleLikePress = ()=>{
    //     addLike(this.state.post._id ,this.props.user._id)
    //     .then(res=>{
    //         console.log(res.data);
    //     })
    //     getSinglePost(this.props.match.params.post_id)
    //     .then(res=>{
    //     this.setState({
    //         ...this.state,
    //         post: res.data.post
    //     })
    //     })      
    // }

    controlBtn=()=>{
        let found = false;
        this.props.user.following.forEach(following_id=>{
            if(following_id===this.state.author._id)
            {
                found=true;
            }
            }
        )
        
       return found? (
       <button className={cn(styles.followBtn)} onClick={this.handleUnfollowPress}>Unfollow</button>
       )
        : (
        <button className={cn(styles.followBtn)} onClick={this.handleFollowPress}>Follow</button>
        )
        
    }
    handleUnfollowPress = () =>{
        console.log("Unfollower ID====>", this.props.user._id);
        removeFollower(this.state.author._id, this.props.user._id)
        .then(res=>{
            console.log(res.data);
            getUser()
            .then(res=>{
            this.props.updateUser(res.data)
            })
            getUserById(this.state.author._id)
            .then(res=>{
                this.setState({
                    ...this.state,
                    author: res.data
                })
                this.props.history.push("/post/"+this.state.post._id)
            })
            })
    }
    handleFollowPress = ()=>{
        console.log("Follower ID====>", this.props.user._id);
        addFollower(this.state.author._id, this.props.user._id)
        .then(res=>{
            console.log(res.data);
            getUser()
            .then(res=>{
            this.props.updateUser(res.data)
            })
            getUserById(this.state.author._id)
            .then(res=>{
                this.setState({
                    ...this.state,
                    author: res.data
                })
                this.props.history.push("/post/"+this.state.post._id)
            })
        })
    }
    likePressControl = ()=>{
        let found = false;
        this.state.post.likes.map((like)=>
           like ===  this.props.user._id? (found = true ): (null)
        )
       return  found ? (
            <IconButton className={cn(styles.likeBtn,styles.pointer)} onClick={this.handleUnlikePress}>
                <FavoriteIcon color="secondary"/>
            </IconButton>
        )
        :(
            <IconButton className={cn(styles.likeBtn,styles.pointer)} onClick={this.handleLikePress}>
                <FavoriteIcon/>
            </IconButton>
        )
    }
    handleLikePress=()=>{
        addLike(this.state.post._id, this.props.user._id)
        .then(res=>{
            getSinglePost(this.state.post._id)
            .then(res=>{
                this.setState({
                ...this.state,
                post: res.data.post
            })
        })
        })
    }
    handleUnlikePress = ()=>{
        removeLike(this.state.post._id, this.props.user._id)
        .then(res=>{
            getSinglePost(this.state.post._id)
            .then(res=>{
            this.setState({
                ...this.state,
                post: res.data.post
            })
        })
        })
    }

    commentsList = ()=>{
        return this.state.commentorsComplete ? (
            this.state.post?.comments.map((comment, index)=>
             <div className={cn(styles.singleComment, styles.horizontalFlex)} key={index}>
            {/* <img className={styles.commentorProfilePic} onClick={()=>this.handleProfileClick((this.state.commentors[index]))} src={this.state.commentors[index]?.profilePic} alt="profile"/> */}
            <h5 className={cn(styles.commentor,styles.pointer)} onClick={()=>this.handleProfileClick((this.state.commentors[index]))}>{this.state.commentors[index]?.firstName} {this.state.commentors[index]?.lastName}</h5> 
            <p className={styles.commentBody} >{comment.body}</p>
            </div>   
        
        )):(null)
    }

    handleProfileClick = (user)=>{
        this.props.updateClickedUser(user);
        if(user._id===this.props.user._id){
            this.props.history.push("/profile");
        }else{
            this.props.history.push("/account");
        }
    }

    render() {
        return (
            (this.state.post && this.state.author)?
      (      <div className={cn(styles.singlePostWrapper)}>
                <div className={cn(styles.horizontalFlex ,styles.singlePost)}>
                    <img className={styles.postImage} src={this.state.post.image} alt="post" />
                    
                    <div className={cn(styles.postInfo, styles.verticalFlex)}>
                    <div className={styles.postHead}>
                        <div className={cn(styles.authorDetails, styles.horizontalFlex)}>
                        <div className={styles.horizontalFlex}>
                            <img className={styles.profilePicCircle} onClick={()=>this.handleProfileClick(this.state.author)} src={this.state.author.profilePic} alt="profile pic"/>
                            <h5 className={styles.authorName} onClick={()=>this.handleProfileClick(this.state.author)} >{this.state.author.firstName} {this.state.author.lastName}</h5>
               
                        </div>
                           
                            {
                                this.state.author._id !== this.props.user._id ? (
                                    this.controlBtn()
                                ):(null)
                            }
                            
                        </div>
                        
                        <div className={cn(styles.horizontalFlex,styles.postDetails)}>
                            <h5 className={cn(styles.captionWriter,styles.pointer)}> {this.state.author.firstName} {this.state.author.lastName}</h5>
                            <p className={cn(styles.caption, styles.horizontalFlex)}>{this.state.post.caption}</p>
                        </div>
                    
                    <div className={styles.commentsArray}>
                            {this.state.commentors !== null && this.commentsList()}
                    </div>
                    </div>
                    <div className={cn(styles.postTail, styles.verticalFlex)}>
                            <div className={cn(styles.likes, styles.verticalFlex)}>
                                {/* <button onClick={this.handleLikePress}>Like</button> */}
                                {this.likePressControl()}
                                <p className={styles.likesNum}>{this.state.post?.likes.length} Likes</p>
                            </div>
                            <div className={cn(styles.addComment, styles.horizontalFlex)}>
                                <input className={styles.inputCommentField} type="text" name="comment" autoComplete="off" placeholder="Write a comment..." value={this.state.inputComment} onChange={this.handleInputCommentChange}/>
                                <button className={styles.postBtn} onClick={this.handleSubmit}>Post</button>
                            </div>
                    </div>
                    </div>
                    
                   
                </div>
            </div>)
            :(null)
           
        )
    }
}

const mapStateToProps = (state)=>{
    return {
        user: state.user,
        posts: state.posts,
        clickedUser: state.clickedUser
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        updateUser: (user) => {
            dispatch(updateUser(user))
        },
        addPosts: (posts)=>{
            dispatch(addPosts(posts))
        },
        updateClickedUser: (user)=>{
            dispatch(updateClickedUser(user))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SinglePost)