import axios from 'axios'

// const baseURL = "http://localhost:5000"
const baseURL = "https://cors-anywhere.herokuapp.com/https://stormy-fortress-78932.herokuapp.com"
//const baseURL = "https://stormy-fortress-78932.herokuapp.com"


export const registerUser=(req)=>{
    return axios({
        method: "POST",
        data: req,
        withCredentials: true,
        url: `${baseURL}/register`
    })
}
// export const login=(username, password)=>{
//     return axios({
