export const getUser=async()=>{
    return await fetch(`${baseURL}/user`,{
         method: "GET",
         headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        referrerPolicy: 'no-referrer',
         credentials: 'same-origin'
     })
 }

