export const getUserById =(id)=>{
    return fetch(`${baseURL}/userbyid`,{
         method: "POST",
         headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
         data: JSON.stringify({id}),
         credentials: 'same-origin'
     })
 }


