import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import styles from './login.module.css'
import { login } from '../utils/authentication';

export class Login extends Component {
    state = {
        username: '',
        password: ''
    }
    handleUsernameChange = (e) =>{
       
        this.setState({
            ...this.state,
            username: e.target.value
        })
    }

    handlePasswordChange = (e) =>{
        this.setState({
            ...this.state,
            password: e.target.value
        })
    }
    handleSubmit=(e)=>{
        login(this.state.username, this.state.password)
        .then(res=>{
            console.log(res.status)
            this.setState({
                ...this.state,
                username: '',
                password: ''
            })
            
            this.props.history.push('/home')
            
        })
      
    }
   
    render() {
        return (
            <div className={styles.login}>
                <div className={styles.signinForm}>
                    <h2 className={styles.formTitle}>Instagram</h2>
                    <input className={styles.loginInput} onChange={this.handleUsernameChange} type="mail" name="text" placeholder="Username or Email" required={true} autoComplete="off" value={this.state.username}/><br/>
                    <input className={styles.loginInput} onChange={this.handlePasswordChange} type="password" name="password" placeholder="Password" required={true} value={this.state.password}/><br/>
                    <button onClick={this.handleSubmit} className={styles.loginBtn}>Log In</button>
                    
                   
                </div>
                <div className={styles.signinForm}>
                    <p>Don't have an account? <NavLink to='/signup'>Sign Up</NavLink></p>
                </div>
            </div>
        )
    }
}

export default Login
