import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import styles from './navbar.module.css'
import {updateUser} from '../actions/updateUser'
import {connect} from 'react-redux'
import {addPosts} from '../actions/addPost'
import {updateClickedUser} from '../actions/updateClickedUSer'
import { logout } from '../utils/authentication'
import ExploreIcon from '@material-ui/icons/Explore';
import HomeIcon from '@material-ui/icons/Home';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

class Navbar extends Component {
    

    handleLogOut =()=>{
        this.props.updateUser('');
        this.props.updateClickedUser('');
        this.props.addPosts('');
        logout()
        .then(res=>{
            this.props.history.push('/');
            console.log(res.data);
        })
      
    }
    render() {
        return (
            <div className={styles.navbarWrapper}>
                <div className={styles.navbar}>
                <NavLink to='/' className={styles.brand}>Instagram</NavLink>
                <div className={styles.nav}>
                    <NavLink to='/home' className={styles.navBtn}><HomeIcon /></NavLink>
                    <NavLink to='/discover' className={styles.navBtn}><ExploreIcon /></NavLink>
                    <NavLink to='/profile' className={styles.navBtn}><AccountCircleIcon /></NavLink>
                    <NavLink to='/' onClick={this.handleLogOut} className={styles.navBtn}><ExitToAppIcon /></NavLink>
                    {/* <img className={cn(styles.navBtn, styles.circleImage)} src={this.props.user.profilePic} alt="profile"/> */}
                </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state)=>{
    return {
        user: state.user
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        updateUser: (user) => {
            dispatch(updateUser(user))
        },
        addPosts: (posts)=>{
            dispatch(addPosts(posts))
        },
        updateClickedUser: (user)=>{
            dispatch(updateClickedUser(user))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Navbar)