import React, { Component } from 'react'
import styles from './account.module.css'
import Tile from './Tile'
import {updateUser} from '../actions/updateUser'
import {addPosts} from '../actions/addPost'
import {connect} from 'react-redux'
import {getPosts, getUser, addFollower, removeFollower, getUserById} from '../utils/authentication'
import {updateClickedUser} from '../actions/updateClickedUSer'


class Account extends Component{
    componentDidMount(){
        //console.log(this.props);
        getPosts(this.props.clickedUser, "SELF_POSTS")
        .then(res=>{
            this.props.addPosts(res.data)
        })
        getUser()
        .then(res=>{
            this.props.updateUser(res.data)
        })
    }

    componentDidUpdate(){
        console.log("UPDATED");
    }
    addPath = (path)=>{
        this.props.history.push(path)
    }
   
    tileList =() => {

           return ( this.props.posts.map((post, index)=>
                <Tile
                    key={index}
                    goToSinglePost={this.addPath}
                    post={post}
                />     
                )
                )
        }
    controlBtn=()=>{
        let found = false;
        this.props.user.following.forEach(following_id=>{
            if(following_id===this.props.clickedUser._id)
            {
                found=true;
            }
            }
        )
        
       return found? (
       <button className={styles.followingBtn} onClick={this.handleUnfollowPress}>Unfollow</button>
       )
        : (
        <button className={styles.followingBtn}  onClick={this.handleFollowPress}>Follow</button>
        )
        
    }
    handleUnfollowPress = () =>{
        console.log("Unfollower ID====>", this.props.user._id);
        removeFollower(this.props.clickedUser._id, this.props.user._id)
        .then(res=>{
            console.log(res.data);
            getUser()
            .then(res=>{
            this.props.updateUser(res.data)
            })
            getUserById(this.props.clickedUser._id)
            .then(res=>{
                this.props.updateClickedUser(res.data)
                this.props.history.push("/account")
            })
            })
    }
    handleFollowPress = ()=>{
        console.log("Follower ID====>", this.props.user._id);
        addFollower(this.props.clickedUser._id, this.props.user._id)
        .then(res=>{
            console.log(res.data);
            getUser()
            .then(res=>{
            this.props.updateUser(res.data)
            })
            getUserById(this.props.clickedUser._id)
            .then(res=>{
                this.props.updateClickedUser(res.data)
                this.props.history.push("/account")
            })
        })
    }
    render(){
        return (
            <div className={styles.accountWrapper}>
            {this.props.clickedUser?(
            <div className={styles.account}>
                <div className={styles.head}>
                    <img className={styles.profilePic} src={this.props.clickedUser.profilePic} alt={this.props.clickedUser.profilePic} />
                    <div className={styles.headRight}>
                        <div className={styles.userEditProfile}>
                            <p className={styles.userName}>{ this.props.clickedUser.username}</p>
                            { this.props.clickedUser?this.controlBtn():null}
                        </div>
                    
                        <div className={styles.headRightItem}>
                            <p className={styles.headRightItemChild}>{this.props.posts.length} posts</p>
                            <p className={styles.headRightItemChild}>{this.props.clickedUser.followers.length} followers</p>
                            <p className={styles.headRightItemChild}>{this.props.clickedUser.following.length} following</p>
                        </div>
                        <p>{ this.props.clickedUser.firstName} { this.props.clickedUser.lastName} <br/>
                        { this.props.clickedUser.bio}</p>
                    </div>
                </div>
             
                <div className={styles.tilesPanel}>
                    {this.tileList()}
                </div>
            </div>
            ):(
                null
            )
            }
          
           
            </div>
        )
    }
}

const mapStateToProps = (state)=>{
    return {
        user: state.user,
        posts: state.posts,
        clickedUser: state.clickedUser
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        updateUser: (user) => {
            dispatch(updateUser(user))
        },
        addPosts: (posts)=>{
            dispatch(addPosts(posts))
        },
        updateClickedUser: (user)=>{
            dispatch(updateClickedUser(user))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Account)