import React, { Component } from 'react'
import styles from './signup.module.css'
import {registerUser} from '../utils/authentication'
import {NavLink} from 'react-router-dom'

class Signup extends Component{
    state = {
        username: '',
        password: '',
        firstName: '',
        lastName: ''
    }

    handleUsernameChange = (e) =>{
        this.setState({
            ...this.state,
            username:  e.target.value
        })
    }
    
    handlePasswordChange = (e) =>{
        this.setState({
            ...this.state,
            password:  e.target.value
        })
    }
    handleFirstNameChange = (e) =>{
        console.log(e.target.value);
        this.setState({
            ...this.state,
            firstName:  e.target.value
        })
    }
    handleLastNameChange = (e) =>{
        this.setState({
            ...this.state,
            lastName:  e.target.value
        })
    }
    handleSubmit=(e)=>{
        const req = {
            username: this.state.username,
            password: this.state.password,
            firstName: this.state.firstName,
            lastName: this.state.lastName
        }
        registerUser(req)
        .then(res=>{console.log(res.data)
        this.props.history.push('/')})
        this.setState({
            ...this.state,
            username: '',
            password:''
        })
    }
    render(){
        return(
            <div className={styles.signup}>
                <div className={styles.signupForm}>
                    <h2 className={styles.formTitle}>Instagram</h2>
                    <input className={styles.signupInput} onChange={this.handleUsernameChange} type="text" placeholder="Username or Email" required={true} autoComplete="on"/><br/>
                    <input className={styles.signupInput} onChange={this.handleFirstNameChange} type="text" placeholder="First Name" required={true} autoComplete="on"/><br/>
                    <input className={styles.signupInput} onChange={this.handleLastNameChange} type="text" placeholder="Last Name" required={true} autoComplete="on"/><br/>
                    <input className={styles.signupInput} onChange={this.handlePasswordChange} type="password" placeholder="Password" required={true}/><br/>
                    <button onClick={this.handleSubmit} className={styles.signupBtn}>Sign Up</button>
                    
                </div>
                <div className={styles.signupForm}>
                     <p>Have an account? <NavLink to='/'>Log In</NavLink></p>
                </div>
            </div>
        )
    }
}

export default Signup