import React, { Component } from 'react'
import { addComment, getUserById ,getSinglePost, removeLike} from '../utils/authentication'
import styles from './snippet.module.css'
import {updateUser} from '../actions/updateUser'
import {connect} from 'react-redux'
import {addPosts} from '../actions/addPost'
import { addLike} from '../utils/authentication'
import {updateClickedUser} from '../actions/updateClickedUSer'
import cn from 'classnames'

import FavoriteIcon from '@material-ui/icons/Favorite';
import IconButton from '@material-ui/core/IconButton';

class Snippet extends Component{
    state={
        post:null,
        author:'',
        commentors:[],
        commentorsComplete: false,
        inputComment:''
    }
    componentDidMount(){
       getUserById(this.props.post.author_id)
       .then(res=>{
           //console.log(res.data);
           this.setState({
               ...this.state,
               author: res.data,
               post: this.props.post
           })
       })
    }
    componentDidUpdate(){
        if(this.state.post !== null && this.state.commentorsComplete === false){
            this.state.post.comments.map(async(comment)=>{
             await getUserById(comment.commentor_id)
             .then(res=>{
                this.setState({
                    ...this.state,
                    commentors: [...this.state.commentors, res.data]
                })
             })
            })
            this.setState({
                ...this.state,
                commentorsComplete: true
            })
            
        }
    }
    likePressControl = ()=>{
        let found = false;
        this.state.post.likes.map((like)=>
           like ===  this.props.user._id? (found = true ): (null)
        )
       return  found ? (
            <IconButton className={cn(styles.likeBtn,styles.pointer)} onClick={this.handleUnlikePress}>
                <FavoriteIcon color="secondary"/>
            </IconButton>
        )
        :(
            <IconButton className={cn(styles.likeBtn,styles.pointer)} onClick={this.handleLikePress}>
                <FavoriteIcon/>
            </IconButton>
        )
    }
    handleLikePress=()=>{
        addLike(this.state.post._id, this.props.user._id)
        .then(res=>{
            getSinglePost(this.state.post._id)
            .then(res=>{
            
            this.setState({
                ...this.state,
                post: res.data.post
            })
        })
        })
    }
    handleUnlikePress = ()=>{
        removeLike(this.state.post._id, this.props.user._id)
        .then(res=>{
            getSinglePost(this.state.post._id)
            .then(res=>{
            
            this.setState({
                ...this.state,
                post: res.data.post
            })
        })
        })
    }
    commentList = ()=>{
       
            return this.state.commentorsComplete ? (
                this.state.post?.comments.map((comment, index)=>
                 <div className={cn(styles.horizontalFlex,styles.singleComment)} key={index}>
                    <h5 className={cn(styles.commentor,styles.pointer)} onClick={()=>this.handleProfileClick((this.state.commentors[index]))}>{this.state.commentors[index]?.firstName} {this.state.commentors[index]?.lastName}</h5>
                    <p className={styles.commentBody}>{comment.body} </p>
                </div>   
            
            )):(null)
    }
    handleInputCommentChange=(e)=>{
        this.setState({
            ...this.state,
            inputComment: e.target.value
        })
    }
    handleCommentAdd = (e)=>{
        const comment = {
            commentor_id: this.props.user._id,
            body: this.state.inputComment
        }
        addComment(this.state.post,comment)
        .then(res=>{
            getSinglePost(this.state.post._id)
            .then(res=>{
            
            this.setState({
                ...this.state,
                post: res.data.post,
                commentorsComplete: false,
                commentors: '',
                inputComment: ''
            })
        })
        })
       
    }
    handleProfileClick = (user)=>{
        this.props.updateClickedUser(user);
        if(user._id===this.props.user._id){
            this.props.addPath("/profile");
        }else{
            this.props.addPath("/account");
        }
    }
    render(){
        return(<div>
{
                this.state.post?(
                    <div className={cn(styles.verticalFlex,styles.snippet)}>
                    {/* ----------------------POST HEAD------------------------ */}
                    {this.state.author?
                    (  <div  className={cn(styles.postHead, styles.verticalFlex)}>
                        <div className={cn(styles.postAuthor,styles.horizontalFlex)}>
                            <img className={cn(styles.profilePic,styles.pointer)} onClick={()=>this.handleProfileClick(this.state.author)} src={this.state.author.profilePic} alt={this.state.author.profilePic}/>
                            <div className={cn(styles.verticalFlex, styles.authorAndLocation)}>
                                <h5 className={cn(styles.authorName,styles.pointer)} onClick={()=>this.handleProfileClick(this.state.author)}> {this.state.author.firstName} {this.state.author.lastName}</h5>
                                <p className={styles.postLocation} >{this.state.post.location} </p>
                            </div>
                        </div>
                        </div>
                      
                        
                    )
                    :(null)}
                    {/* ----------------------POST IMAGE------------------------ */}
                    <img onClick={()=>this.props.addPath("/post/"+this.state.post._id)} className={styles.postImage} src={this.state.post.image} alt="post"/>
                    
                    {/* ----------------------POST TAIL------------------------ */}
                    <div className={styles.postTail}>
                    {/* ----------------------POST LIKE------------------------ */}
                    <div className={styles.likes}>
                    {/* <button className={cn(styles.likeBtn,styles.pointer)} onClick={this.handleLikePress}><FontAwesomeIcon icon={faHeart}/></button> */}
                    {this.likePressControl()}
                    <p className={cn(styles.likesNumber)}>Liked by {this.state.post.likes.length} others</p>
                    </div>
                    
    
                    {/* ----------------------POST DETAILS------------------------ */}
                    <div className={cn(styles.horizontalFlex,styles.postDetails)}>
                        <h5 className={cn(styles.authorName,styles.pointer)}> {this.state.author.firstName} {this.state.author.lastName}</h5>
                        <p className={cn(styles.caption, styles.horizontalFlex)}>{this.state.post.caption}</p>
                    </div>

                    {/* ----------------------POST COMMENTS------------------------ */}
                    <div className={styles.postComments}>
                    {this.state.commentorsComplete && this.commentList()}
                    </div>
                    
                    </div>
                   {/* ----------------------ADD COMMENT------------------------ */}
                   <div className={cn(styles.horizontalFlex,styles.inputComment)}>
                   <input className={cn(styles.inputCommentField)} type="text" onChange={this.handleInputCommentChange} placeholder="Write a comment" value={this.state.inputComment} autoComplete="off"/>
                   <button className={cn(styles.addBtn,styles.pointer)} onClick={this.handleCommentAdd}>Post</button>
                   </div>
                    
                </div>
                )
                :(null)
            }
        </div>
            
           
        )
    }
}


const mapStateToProps = (state)=>{
    return {
        user: state.user,
        posts: state.posts,
        clickedUser: state.clickedUser
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        updateUser: (user) => {
            dispatch(updateUser(user))
        },
        addPosts: (posts)=>{
            dispatch(addPosts(posts))
        },
        updateClickedUser: (user)=>{
            dispatch(updateClickedUser(user))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Snippet)