import React, { Component } from 'react'
import { getUser, getPosts} from '../utils/authentication'
import styles from './home.module.css'
import {updateUser} from '../actions/updateUser'
import {connect} from 'react-redux'
import {addPosts} from '../actions/addPost'
import Snippet from './Snippet'
import {updateClickedUser} from '../actions/updateClickedUSer'


class Home extends Component{
    state={
        flag: false
    }
    componentDidMount(){
        getUser()
        .then(res=>{
            console.log(res);
            // this.props.updateUser(res.data);
            // getPosts(this.props.user, "FOLLOWING_POSTS")
            // .then(res=>{
            //     this.props.addPosts(res.data);
            //     this.setState({
            //         ...this.state,
            //         flag:true
            //     })
            //     console.log("props=====>",this.props);
            //     console.log("state=====>",this.state);
            // })
        })
       
       
    }
  
    addPath = (path)=>{
        console.log("Path added");
        this.props.history.push(path);
    }
    tileList =() => {
        return (
            this.state.flag?(
                this.props.posts.map((post, index)=>
                <div key={index} className={styles.singlePost}>
                    <Snippet
                        addPath={this.addPath}
                        post = {post}
                    />   
                </div>
                 )
           
             ):(
                 <h1 className={styles.center}>You are not following anyone</h1>
             )
             )
            
     }
 
    render(){
        return(
            <div className={styles.homeWrapper}>
            <div className={styles.home}>
                <div className={styles.posts}>
                {this.tileList()}
                </div>
                    
            </div>
           
            </div>

        )
    }
}

const mapStateToProps = (state)=>{
    return {
        user: state.user,
        posts: state.posts,
        clickedUser: state.clickedUser
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        updateUser: (user) => {
            dispatch(updateUser(user))
        },
        addPosts: (posts)=>{
            dispatch(addPosts(posts))
        },
        updateClickedUser: (user)=>{
            dispatch(updateClickedUser(user))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home)