import React, { Component } from 'react'
import styles from './editProfile.module.css'
import {updateUser} from '../actions/updateUser'
import {addPosts} from '../actions/addPost'
import {connect} from 'react-redux'
import { editUser } from '../utils/authentication'


class EditProfile extends Component{
    state = {
       selectedFile: null,
       img: null
    }
    handleFormSubmit=(e)=>{
        const inputData = {
            profilePic: this.state.img,
            fName: e.target.fName.value,
            lName: e.target.lName.value,
            email: e.target.email.value,
            phone: e.target.phone.value,
            bio: e.target.bio.value,
            gender: e.target.gender.value,

        }
        editUser(this.props.user._id,inputData)
        .then(res=>{
            console.log(res.data);
            this.props.updateUser(res.data);
            this.addPath("/profile");
        })
        e.preventDefault();
    }
    addPath = (path)=>{
        this.props.history.push(path)
    }
    handleProfileImage = e=>{
        const file = e.target.files[0]
        const reader = new FileReader();
      
        reader.addEventListener("load",  () =>{
       
        console.log('========>',reader.result)
        this.setState({img:reader.result})
        }, false);
      
        if (file) {
          reader.readAsDataURL(file);
        }
    }
   

    render(){
        return(
            <div className={styles.editProfileWrapper}>
            {
                this.props.user?(
                    <div className={styles.editProfile}>
                    
        
                    <form className={styles.editProfileForm} onSubmit={this.handleFormSubmit}>
                        <label>Profile Picture: </label>
                        <input onChange={this.handleProfileImage} type="file" name="profilePic"/><br/>
                        <label>First Name: </label>
                        <input type="text" name="fName" autoComplete="off" /><br/>
                        <label>Last Name: </label>
                        <input type="text" name="lName" autoComplete="off" /><br/>
                        <label>Email: </label>
                        <input type="email" name="email" autoComplete="off" /><br/>
                        <label>Bio: </label>
                        <input type="text" name="bio" autoComplete="off" /><br/>
                        <label>Phone Number: </label>
                        <input type="number" name="phone" autoComplete="off" /><br/>
                        <label>Gender: </label>
                        <input type="radio" id="male" name="gender" value="male"/>
                        <label>Male</label>
                        <input type="radio" id="female" name="gender" value="female"/>
                        <label>Female</label><br/>
                        <button type="submit">Submit</button>
                    </form>
                    </div>
                ):(null)
            }

            </div>
        )
    }
}
const mapStateToProps = (state)=>{
    return {
        user: state.user,
        posts: state.posts
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        updateUser: (user) => {
            dispatch(updateUser(user))
        },
        addPosts: (posts)=>{
            dispatch(addPosts(posts))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(EditProfile)