import React, { Component } from 'react'
import styles from './profile.module.css'
import Tile from './Tile'
import {updateUser} from '../actions/updateUser'
import {addPosts} from '../actions/addPost'
import {connect} from 'react-redux'
import {getPosts} from '../utils/authentication'
import EditIcon from '@material-ui/icons/Edit';
import IconButton from '@material-ui/core/IconButton'
import AddCircleIcon from '@material-ui/icons/AddCircle';
import cn from 'classnames'

class Profile extends Component{
    componentDidMount(){
        getPosts(this.props.user, "SELF_POSTS")
        .then(res=>{
            this.props.addPosts(res.data)
        })
    }
    addPath = (path)=>{
        this.props.history.push(path)
    }
    handleAddPostClick = () =>{
        this.props.history.push('/addpost');
    }
 
    tileList =() => {
        if(this.props.posts){
        return ( 
            this.props.posts.map((post, index)=>
            <Tile
                key={index}
                goToSinglePost={this.addPath}
                post={post}
            />     
            )
            )
            }
    }
    handleEditClick = ()=>{
        console.log("Edit Profile clicked");
        this.props.history.push('/editprofile');
    }
    render(){
        return (
            <div className={styles.profileWrapper}>
            {this.props.user?(
            <div className={styles.profile}>
            <div className={cn(styles.headWrapper, styles.verticalFlex)}>
                <div className={cn(styles.horizontalFlex, styles.head)}>
                    <img className={styles.profilePic} src={this.props.user.profilePic} alt={this.props.user.profilePic} />
                    <div className={cn(styles.verticalFlex ,styles.headRight)}>
                        <div className={cn(styles.horizontalFlex ,styles.userEditProfile)}>
                            <p className={styles.userName}>{ this.props.user.username}</p>
                            <IconButton className={styles.editProfileBtn} onClick={this.handleEditClick}>
                            <EditIcon />
                            </IconButton>
                        </div>
                    
                        <div className={cn(styles.horizontalFlex ,styles.headRightItem)}>
                            <p className={styles.headRightItemChild}>{this.props.posts.length} posts</p>
                            <p className={styles.headRightItemChild}>{this.props.user.followers?.length} followers</p>
                            <p className={styles.headRightItemChild}>{this.props.user.following?.length} following</p>
                        </div>
                        <p className={styles.userFLName}>{ this.props.user.firstName} { this.props.user.lastName}</p>
                        { this.props.user.bio}
                    </div>
                </div>
                <IconButton className={styles.addPostBtn} onClick={this.handleAddPostClick}>
                    <AddCircleIcon fontSize="large"/>
                </IconButton>
                </div>
                <div className={styles.tilesPanel}>
                    {this.tileList()}
                </div>
            </div>
            ):(
                null
            )
            }
          
           
            </div>
        )
    }
}

const mapStateToProps = (state)=>{
    return {
        user: state.user,
        posts: state.posts
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        updateUser: (user) => {
            dispatch(updateUser(user))
        },
        addPosts: (posts)=>{
            dispatch(addPosts(posts))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Profile)