import React, { Component } from 'react'
import styles from './addPost.module.css'
import {updateUser} from '../actions/updateUser'
import {addPosts} from '../actions/addPost'
import {connect} from 'react-redux'
import {addPost} from '../utils/authentication'

class AddPost extends Component{
    state={
        postImage:''
    }
    handleSubmit = (e) =>{
        const postData={
            location: e.target.location.value,
            caption: e.target.caption.value,
            id: this.props.user._id,
            postImage: this.state.postImage
        }
        addPost(postData)
        .then(res=>{console.log(res.data)
        })
        this.props.history.push('/profile')
        e.preventDefault();
    }
    handleImageChange = (e)=>{
        const file = e.target.files[0]
        const reader = new FileReader();
      
        reader.addEventListener("load",  () =>{
        console.log(reader.result);
        this.setState({postImage:reader.result})
        }, false);
      
        if (file) {
          reader.readAsDataURL(file);
        }
    }
    render(){
        return(
            <div className={styles.addPost}>
                <form className={styles.postForm} onSubmit={this.handleSubmit}>
                    <label>Image: </label>
                    <input type="file" onChange={this.handleImageChange}/><br/>
                    <label>location: </label>
                    <input type="text" name="location" autoComplete="off"/><br/>
                    <label>caption: </label>
                    <input type="text" name="caption" autoComplete="off"/><br/>
                    <button type="submit">Add</button>
                </form>
            </div>
        )
    }
}


const mapStateToProps = (state)=>{
    return {
        user: state.user,
        posts: state.posts
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        updateUser: (user) => {
            dispatch(updateUser(user))
        },
        addPosts: (posts)=>{
            dispatch(addPosts(posts))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AddPost)